<template>
	<iframe class="scene_iframe"
		ref="sceneIframe"
		:src="threeEditorController.iframeSrc"
		v-on:load="handleSceneIframeLoad">
	</iframe>
</template>

<script>
	export default {
		inject: ['threeEditorController'],

		methods: {
			handleSceneIframeLoad( e ){

				// const iframeWindow = this.$refs.sceneIframe.contentWindow;

				// console.log('iframeWindow.settings:', iframeWindow.settings);

				const { settings, scene, camera } = e.currentTarget.contentWindow;

				this.threeEditorController.settings = settings;
				this.threeEditorController.scene = [scene];
				this.threeEditorController.camera = camera;
			}
		}
	}
</script>

<style lang="less">
	.scene_iframe{
		width: 100%;
		height: 100%;
	}
</style>